import React from "react";
import Paginator from "../common/Paginator/Paginator";
import User from "./User";

let Users = (props) => {

    let pagesCount = Math.ceil(props.totalUsersCount / props.pageSize);

    let pages = [];
    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i);
    }

    return (
        <div>
            <Paginator totalUsersCount={props.totalUsersCount} pageSize={props.pageSize} onPageChanged={props.onPageChanged} currentPage={props.currentPage } />
            {
                props.users.map(user => <User user={user} toggleFollowingProgress={props.toggleFollowingProgress} key={user.id} followingInProgress={props.followingInProgress} unfollow={props.unfollow} follow={props.follow}/> )

            }
        </div>
    )
}


export default Users;